<template>
    <div class="data-box">
       
      
         <van-nav-bar
            title="个人资料"
            left-text="返回"
            left-arrow
            fixed
            @click-left="back();changeNick(AccountData.nickName);changeDesc(AccountData.desc);changeImg(AccountData.userImg)"
        />
            <!-- @click="changeNick(AccountData.desc)" -->
        <div class="data-bg"></div>
        <div class="data-box">
            <!-- v-for="(item,index) in AccountData" :key="index" -->
            <div class="data-content">
                 <div class="item">
                    <div class="item-title">头像</div>
                    <!-- <div class="item-value pic" :style="{ backgroundImage: `url(${AccountData.userImg})` }"></div> -->
                    <div class="item-value pic" :style="{ backgroundImage: `url(${AccountData.userImg})` }">
                        <van-uploader class="uploader" :after-read="updateImg" />
                       
                    </div>
                    
                </div>
                <div class="item">
                    <div class="item-title">用户id</div>
                     <!-- <input type="text" v-model="AccountData.userId"  class="item-ipt"/> -->
                       <div class="item-value">{{AccountData.userId}}</div>
                   
                </div>
                <div class="item">
                    <div class="item-title">昵称</div>
                     <!-- <div class="item-value">abc</div> -->
                      <input type="text" v-model="AccountData.nickName" class="item-ipt" @blur="niCheng()"/>
                     <!-- <div class="item-value">{{item.nickName}}</div> -->
                </div>
                <div class="item jianjie">
                    <div class="item-title">简介</div>
                    <!-- <input type="text" v-model="AccountData.length == 0 ? AccountData.desc : '这家伙很懒,什么都没有留下!'" class="item-ipt"/> -->
                    <input type="text" v-model="AccountData.desc" class="item-ipt" @blur="jianJie()"/>
                </div>
                </div>
            </div>
        </div>
</template>

<script>
// import{mapState,mapMutations, mapActions} from 'vuex';
    export default {
        name: 'PersonalData',
        created(){
            this.findAccountInfo();
            this.updateDesc(this.v1);
           
        },
        data(){
            return{
                AccountData:[],
                val:"",
                descDate:"",
                isOpen:true,
                v1:"",
                iptVal:""
              
            }
        },
        methods:{
            changeNick(txt){
               this.$store.commit("changeNick",txt);
            //    console.log("=======>",131232);
           },
            changeDesc(txt){
               this.$store.commit("changeDesc",txt);
            //    console.log("=======>",131232);
           },
            changeImg(txt){
               this.$store.commit("changeImg",txt);
            //    console.log("=======>",131232);
           },
            // //上传头像
        updateImg(file) {
            //获取base64
            let base64 = file.content.replace(/^data:image\/[A-Za-z]+;base64,/, '');
            // 

            //获取token
            let tokenString = this.$cookies.get("TOKEN");

            this.axios({
                method: "POST",
                url: "/updateAvatar",
                data: {
                tokenString,
                serverBase64Img: base64,
                imgType: file.file.type.split(/\//)[1]
                },
            })
                .then((res) => {
                console.log("updateAvatar ===>",res);
                if (res.data.code == 700) {
                    //用户未登录, 拦截操作, 通知用户去登录
                    this.$toast("请先登录");
                    setTimeout(() => {
                    this.$router.push({ name: "Login" });
                    }, 2000);

                    return;
                }

                this.$toast(res.data.msg);

                if (res.data.code === 'H001') {
                    console.log(res.data.userImg);
                    this.AccountData.userImg = res.data.userImg;
                }

                })
                .catch((err) => {
                    console.log(err);
                });
        },
           niCheng(){
               let nickName = this.AccountData.nickName;
                 //获取token
                let tokenString = this.$cookies.get("TOKEN");

                this.axios({
                    method: "POST",
                    url: "/updateNickName",
                    // 修改昵称失败,可能是因为url写错了
                    data: {
                        tokenString,
                        // 得不到数据,是因为data里需要提供的数据没写对
                        nickName
                    },
                })
                    .then((res) => {
                    console.log("updateNickName ========>",res);
                    if (res.data.code == 700) {
                        //用户未登录, 拦截操作, 通知用户去登录
                        this.$toast("请先登录");
                        setTimeout(() => {
                        this.$router.push({ name: "Login" });
                        }, 2000);
                        return;
                    }
                    this.$toast(res.data.msg)
                        if(res.data.code == "C001"){
                            this.AccountData.nickName = res.data.nickName;
                        } 
                    })
                    .catch((err) => {
                        console.log(err);
                    });
           },
            jianJie(){
                let desc = this.AccountData.desc;
                 //获取token
                let tokenString = this.$cookies.get("TOKEN");

                this.axios({
                    method: "POST",
                    url: "/updateDesc",
                    // 修改昵称失败,可能是因为url写错了
                    data: {
                        tokenString,
                        // 得不到数据,是因为data里需要提供的数据没写对
                        desc:desc
                    },
                })
                    .then((res) => {
                    console.log("updateDesc ========>",res);
                    if (res.data.code == 700) {
                        //用户未登录, 拦截操作, 通知用户去登录
                        this.$toast("请先登录");
                        setTimeout(() => {
                        this.$router.push({ name: "Login" });
                        }, 2000);
                        return;
                    }
                    this.$toast(res.data.msg)
                        if(res.data.code == "D001"){
                            this.AccountData.desc = res.data.desc;
                            console.log("====>",this.AccountData.desc);
                            if(this.AccountData.desc.length == 0){
                                this.AccountData.desc = "这家伙很懒,没有留下东西!"
                                this.$toast("已设置默认内容")
                            }
                        }
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            },
           
            queding(){
                console.log(this.AccountData.userId);

            },
          
            // 方法名不可以跟属性名同名
            back(){
                this.$router.go(-1);
            },
            isShow(){
                this.isOpen = false;
            },
            enter(v1){
                // 即使函数有传值,但是事件中函数没有传值,会得不到值
                console.log("失去焦点==>",v1);
                if(this.mydesc.length == 0){
                    this.mydesc = "这家伙很懒,什么也没留下!"
                }
                this.v1 = v1;
                this.updateDesc(this.v1);

            },
            empty(){
                console.log("获取焦点 ===>123");
                this.mydesc = ""
            },
            updateDesc(v){
                //获取token
                let tokenString = this.$cookies.get("TOKEN");

                this.axios({
                    method: "POST",
                    url: "/updateDesc",
                    // 修改昵称失败,可能是因为url写错了
                    data: {
                        tokenString,
                        // 得不到数据,是因为data里需要提供的数据没写对
                        desc:v
                    },
                })
                    .then((res) => {
                    console.log("updateDesc ===>",res);
                    if (res.data.code == 700) {
                        //用户未登录, 拦截操作, 通知用户去登录
                        this.$toast("请先登录");
                        setTimeout(() => {
                        this.$router.push({ name: "Login" });
                        }, 2000);
                        return;
                    }
                        if(res.data.code == "D001"){
                            this.descDate = res.data.desc;
                            // console.log(this.descDate);
                        }
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            },
            // 个人资料
            findAccountInfo(){
                //获取token
                let tokenString = this.$cookies.get("TOKEN");

                this.axios({
                    method: "GET",
                    url: "/findAccountInfo",
                    params: {
                    tokenString,
                    },
                })
                    .then((res) => {
                    console.log("findAccountInfo ===>",res);
                    if (res.data.code == 700) {
                        //用户未登录, 拦截操作, 通知用户去登录
                        this.$toast("请先登录");
                        setTimeout(() => {
                        this.$router.push({ name: "Login" });
                        }, 2000);
                        return;
                    }
                        if(res.data.code == "B001"){
                            this.AccountData = res.data.result[0];
                            if(this.AccountData.desc.length == 0){
                                this.AccountData.desc = "这家伙很懒,什么也没留下!"
                            }
                            console.log("AccountData =========>",this.AccountData.desc);
                            // changeNick(this.AccountData.desc);
                        }
                    })
                    .catch((err) => {
                        console.log(err);
                    });
                        }
        }
    }
</script>

<style lang="less" scoped>
    .data-box{
        /deep/ .van-uploader__upload{
            width: 60px;
            height: 60px;
            border-radius: 50%;
            opacity: 0;
        }
        /deep/ .data-box .item-ipt[data-v-fb597378]{
            width: 170px;
        }
        .item-ipt{
            margin-left: auto;
            border: none;
            color: #a1a1a2;
            // background-color: #ddd;
        }
        .jianjie{
            display: flex;
        }
        .item{
            display: flex;
            padding: 15px 0;
            font-size: 16px;
            border-bottom: 1px solid #f9f9f9;
            align-items: center;
            &:last-of-type{
                border-bottom: none;
                font-size: 14px;
            }
            &:first-of-type{
                padding: 0;
            }
        }
        .pic{
            width: 60px;
            height: 60px;
            // background-color: tomato;
            border-radius: 50%;
            background-repeat: no-repeat;
            background-size: cover;
        }
        .item-value{
            margin-left: auto;
            color: #b8bab2;
        }
        .data-bg{
            margin-top: 46px;
            height: 100px;
            background-color: #eb2403;
        }
        .data-box{
            padding: 10px;
        }
        .data-content{
            background-color: #fff;
            border-top-right-radius: 10px;
            border-top-left-radius: 10px;
            margin-top: -30px;
            padding: 20px 10px 10px;
        }
        /deep/ .van-nav-bar .van-icon {
          color: #eb2403;
        }
        /deep/ .van-nav-bar__text {
          color: #eb2403;
        }
    }
</style>